<template>
	<v-container>
		<loading v-if="!currentRace" />
		<div v-else>
			<v-row dense>
				<v-col v-for="(selection, idx) in currentRaceSorted" :key="idx">
					<selection-card
						:selection="selection"
						:index="+selection.index"
					/>
				</v-col>
			</v-row>
		</div>
	</v-container>
</template>

<script>
import Loading from "@/common/components/Loading.vue";
import { mapState, mapActions } from "vuex";
import SelectionCard from "../components/SelectionCard.vue";
import Tooltip from "@/common/components/Tooltip.vue";
export default {
	components: { SelectionCard, Loading, Tooltip },
	computed: {
		...mapState("seller", ["currentRace"]),
		...mapState("common", ["currentUser"]),
		currentRaceSorted() {
			if (Array.isArray(this.currentRace)) {
				return (
					this.currentRace 
					&&
					this.currentRace
						.map((v, index) => {
							return { ...v, index };
						})
						.sort(
							(a, b) =>
								Boolean(a.selected) - +Boolean(b.selected) ||
								Boolean(a.pictures[this.currentUser.id]) -
									+Boolean(b.pictures[this.currentUser.id]) ||
								+Object.entries(a.pictures).length -
									+Object.entries(b.pictures).length
						)
				);
			}
		},
	},
	data() {
		return {
			word: "",
			loading: false,
		};
	},
	methods: {
		async addSelection() {
			if (!this.word) {
				this.$toast.error("Word must not be void");
			}
			try {
				await createSelection(this.word);
				this.$toast.success(`Added ${this.word} quest`);
				this.word = "";
				return true;
			} catch (e) {
				this.$toast.error(e.message);
				return false;
			}
		},
		async addAndMove2Selection() {
			this.loading = true;
			if (await this.addSelection()) {
				this.$router.push(
					"/seller/selection/" + this.currentRace.length
				);
			}
			this.loading = false;
		},
	},
};
</script>

