<template>
	<v-card
		min-width="300px"
		max-width="300px"
		:class="
			'all-card ' +
			(selection.selected ? 'selected-card' : 'selection-card')
		"
		@click="openSelection"
		elevation="5"
	>
		<v-card-title style="padding: 20px">
			<v-row>
				<v-icon v-if="done" outlined color="green">mdi-check</v-icon>
				<div>{{ selection.word }}</div>
				<v-spacer />
				<v-badge
					:color="renderColor"
					:content="picturesLength || '0'"
					v-if="!selection.selected"
				>
					<v-icon> mdi-image </v-icon>
				</v-badge>
				<div class="winner-info" v-else>by {{ selectionWinner }}</div>
			</v-row>
		</v-card-title>
		<v-card-text>
			<v-img
				v-if="selection.selected"
				:src="selection.selected.thumbnail"
				:lazy-src="selection.selected.thumbnail"
				contain
				max-height="100px"
				min-height="100px"
			>
				<template slot="placeholder">
					<img-placeholder />
				</template>
			</v-img>
			<v-img
				v-else-if="done"
				:src="myPicture.thumbnail"
				:lazy-src="myPicture.thumbnail"
				contain
				max-height="100px"
				min-height="100px"
			>
				<template slot="placeholder">
					<img-placeholder />
				</template>
			</v-img>
			<div v-else-if="picturesLength">
				<v-carousel
					hide-delimiters
					show-arrows-on-hover
					cycle
					:interval="slideInterval"
					hide-delimiter-background
					height="100px"
				>
					<div
						v-for="(pict, index) in selection.pictures"
						:key="index"
						:text="pict.title"
					>
						<v-carousel-item>
							<v-row
								class="fill-height"
								align="center"
								justify="center"
							>
								<v-img
									class="my-3"
									contain
									:src="pict.thumbnail"
									:lazy-src="pict.thumbnail"
									max-height="100%"
									min-height="100%"
								>
									<template slot="placeholder">
										<img-placeholder />
									</template>
								</v-img>
							</v-row>
						</v-carousel-item>
					</div>
				</v-carousel>
			</div>
			<v-alert v-else type="success">Awaiting proposals!!</v-alert>
		</v-card-text>
	</v-card>
</template>

<script>
import ImgPlaceholder from "@/common/components/ImgPlaceholder.vue";
import { mapState } from "vuex";
export default {
	components: { ImgPlaceholder },
	props: {
		selection: Object,
		index: { type: Number, requied: true },
	},
	methods: {
		openSelection() {
			this.$router.push("/seller/selection/" + (this.index + 1));
		},
	},
	computed: {
		...mapState("common", ["currentUser"]),
		picturesLength() {
			return Object.entries(this.selection.pictures).length;
		},
		slideInterval() {
			return 1300 + Math.random() * 400;
		},
		done() {
			return Boolean(this.selection.pictures[this.currentUser.id]);
		},
		myPicture() {
			return this.selection.pictures[this.currentUser.id];
		},
		renderColor() {
			return Object.entries(this.selection.pictures).length < 5
				? "green"
				: Object.entries(this.selection.pictures).length < 15
				? "yellow darken-2"
				: "red";
		},
		selectionWinner() {
			let ret = this.selection && this.selection.selected.user;
			return ret === this.currentUser.id ? "YOU" : ret;
		},
	},
};
</script>

<style>
.all-card {
	margin: 20px;
}
.selection-card {
	cursor: pointer;
}
.selection-card:hover {
	background-color: gainsboro !important;
}
.winner-info {
	color: green;
	font-size: small;
	font-weight: bold;
}
</style>
